
<template>
    <b-row>
      <b-col md="3" lg="3" xl="5">
          {{treeData}}
      </b-col>
      <b-col md="9" lg="9" xl="7"></b-col>
    </b-row>
</template>
  
<script>
import LiquorTree from 'liquor-tree'

export default {
  name: "Tree",
  components: {
    LiquorTree,
  },
  data : function(){
    return {
      treeData: this.getBookRoot(),
      node: {text: "", data: {text:""}}
    }
  },
  methods: {
    getBookRoot(){
      return this.$backend.$getBookRoot().then((response) => {return response});
    },

  },
}
</script>





